import 'flexboxgrid';
import './style/style.scss';

var hello = document.querySelector('.Header-hello');
var nom = document.querySelector('.Header-name');
var job = document.querySelector('.Header-job');
var paris = document.querySelector('.Header-city');






hello.addEventListener('mouseover', function(){
  hello.classList.add('space');


})

hello.addEventListener('mouseout', function(){
  hello.classList.remove('space');


})

nom.addEventListener('mouseover', function(){
  nom.classList.add('space');

})

nom.addEventListener('mouseout', function(){
  nom.classList.remove('space');

})

job.addEventListener('mouseover', function(){
  job.classList.add('space');

})

job.addEventListener('mouseout', function(){
  job.classList.remove('space');

})

paris.addEventListener('mouseover', function(){
  paris.classList.add('space');

})

paris.addEventListener('mouseout', function(){
  paris.classList.remove('space');

})

hello.addEventListener('click',function(){
  hello.style.display='none';
  nom.style.display='block';
})

nom.addEventListener('click',function(){
  nom.style.display='none';
  job.style.display='block';
})

job.addEventListener('click',function(){
  job.style.display='none';
  paris.style.display='block';
})

paris.addEventListener('click',function(){
  paris.style.display='none';
  hello.style.display='block';
})
